import React, { PureComponent } from "react";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4plugins_wordCloud from "@amcharts/amcharts4/plugins/wordCloud";
import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../utils";
import { Card, CardBody, CardHeader } from "react-bootstrap";
import { baseUrl } from "../../../../../utils";

class Tagcloud extends PureComponent {
  state = {
    noData: false,
    isLoading: true,
  };

  componentDidMount() {
    var chart = am4core.create("wordCloud", am4plugins_wordCloud.WordCloud);
    var series = chart.series.push(new am4plugins_wordCloud.WordCloudSeries());

    series.accuracy = 4;
    series.step = 15;
    series.rotationThreshold = 0.7;
    series.maxCount = 200;
    series.minWordLength = 2;
    series.fontFamily = "inter, sans-serif";
    series.maxFontSize = am4core.percent(30);
    chart.logo.disabled = true;
    series.colors = new am4core.ColorSet();
    series.angles = [0, -90];
    series.fontWeight = "600";
    series.dataFields.word = "tag";
    series.dataFields.value = "count";
    series.labels.template.propertyFields.fill = "color";
    series.labels.template.tooltipText = "{word}:\n[bold]{value}[/]";
    series.data = this.props.wordCloud;
    setTimeout(() => this.setState({ isLoading: false }), 2000);
  }

  render() {
    const { isLoading } = this.state;
    const { wordCloud, word_loader } = this.props;

    console.log(isLoading, "isLoadingisLoading", this.props.wordCloud);

    return (
      <>
        <Card className="sw-card-custom gutter-b card-shadow mb-5">
          <CardHeader className="p-5">
            <h6 class="card-label font-weight-bolder m-0"> Word Cloud</h6>
          </CardHeader>
          <CardBody className="p-5">
            {word_loader ? (
              <div class="text-center">
                <div class="analytics_overview_chart">
                  <img
                    class="img-fluid"
                    src="https://cloud.tagbox.com/app/analytics/svg/word-cloud-1.svg"
                  />
                </div>
              </div>
            ) : (
              <>
                {isLoading || !wordCloud.length ? (
                  <SVG
                    className="text-muted"
                    src={toAbsoluteUrl("/svg/word-cloud-1.svg")}
                    height={370}
                    width={598}
                  />
                ) : (
                  <div className="overflow-hidden">
                    <div
                      id="wordCloud"
                      style={{
                        width: "100%",
                        minHeight: 350,
                        display: !isLoading ? `block` : `none`,
                      }}
                    ></div>
                  </div>
                )}
              </>
            )}
          </CardBody>
        </Card>
      </>
    );
  }
}

export default Tagcloud;
